import "./product.scss";

function Product(props) {
    return (
        <div
            className="ProductBlock">
            <div>
                <p className="HoverText">
                    Quick View
                </p>
                <img
                    src={props.photo}
                    alt=""
                    className="Image" />
                <p className="Title">
                    {props.name}
                </p>
                <p className="Price">
                    {props.price}{'$'}
                </p>
            </div>
        </div>
    );
}

export default Product;
